import React from "react"
import { Surface, Text, ZoomedImg } from "./ui"
import { featureVis } from "./helpers"

export default ({ id }) => {
  const [layer, neuron] = id.split(":")
  const friendlyLayer = layer => layer.replace(/mixed/g, "")

  return (
    <Surface
      display="inline-flex"
      flexFlow="row"
      paddingX={1}
      paddingY={1}
      transform="translateY(9px)"
      alignSelf="center"
      border
      borderRadius={5}
      marginLeft={3}
      alignItems="center"
    >
      <div style={{ display: "inline", position: "relative" }}>
        <ZoomedImg size={30} src={featureVis("inceptionv1", layer, neuron)} />
      </div>{" "}
      <Text marginLeft={3} paddingRight={3}>
        {friendlyLayer(layer)}:{neuron}
      </Text>
    </Surface>
  )
}
