import { Icon } from "evergreen-ui"
import React from "react"
import { Text, Surface } from "./ui"

export default class NextButton extends React.Component {
  render() {
    const { title, href } = this.props
    const hoverColor = "#0a85ea"
    const baseColor = "rgba(0, 0, 0, 0.8)"

    return (
      <Surface
        alignItems="flex-end"
        alignSelf="center"
        marginTop={20}
        paddingBottom={40}
      >
        <Surface
          paddingLeft={80}
          marginTop={40}
          onClick={() => {
            if (href) {
              this.setState({ to: href })
            }
          }}
          opacity={href ? 1 : 0.4}
          cursor={href && "pointer"}
        >
          {hovering => (
            <React.Fragment>
              <a href={href} style={{ border: "none" }}>
                <Surface flexFlow="row" alignItems="center">
                  <Text
                    fontSize={30}
                    fontWeight={600}
                    transition="all 300ms ease-out"
                    color={href && hovering ? hoverColor : baseColor}
                    marginLeft={10}
                  >
                    {title}
                  </Text>
                  <Icon
                    icon="chevron-right"
                    size={24}
                    color={hovering ? hoverColor : baseColor}
                    transition="all 600ms ease-out"
                    marginLeft={5}
                    transform={"translateX(" + (hovering ? 10 : 0) + "px)"}
                    marginTop={3}
                  />
                </Surface>
              </a>
            </React.Fragment>
          )}
        </Surface>
      </Surface>
    )
  }
}
