import React, { Component } from "react"
import ReactMarkdown from "react-markdown/with-html"
import { isString } from "lodash"
import { Text, Surface } from "./ui"
import TextNeuron from "./textNeuron"
import Next from "./next"

const Paragraph = ({ children, ...props }) => (
  <Text
    gridColumn="text"
    fontSize={17}
    lineHeight={1.5}
    marginY={10}
    // display="inline-flex"
    alignItems="center"
    flexFlow="row"
    color="black"
    {...props}
  >
    {children}
  </Text>
)

const headingToHref = text =>
  text
    .toLowerCase()
    .replace(/[.,\/#!$%\^&\*;:{}=\-_`~()]/g, "")
    .replace(/\ /g, "-")

/*
  Padding is to leave space at the top of the browser
  for when the user is linked there with #foo
*/
const Heading2 = ({ fontSize = 20, children, ...props }) => {
  return (
    <Surface
      paddingTop={40}
      paddingBottom={20}
      gridColumn="text"
      id={headingToHref(children[0].props.value)}
    >
      <Text color="black" fontSize={fontSize} fontWeight={600} {...props}>
        {children}
      </Text>
    </Surface>
  )
}

const Heading = ({ fontSize = 30, children, ...props }) => (
  <Surface
    borderBottom
    marginBottom={24}
    paddingTop={32}
    paddingBottom={16}
    gridColumn="text"
    id={headingToHref(children[0].props.value)}
  >
    <Text fontSize={fontSize} lineHeight="20px" fontWeight={600} {...props}>
      {children}
    </Text>
  </Surface>
)

export default class Article extends Component {
  render() {
    const {
      markdown,
      images = {},
      figures = {},
      editing = null,
      next,
    } = this.props

    return (
      <React.Fragment>
        <ReactMarkdown
          source={markdown}
          escapeHtml={false}
          skipHtml={false}
          renderers={{
            heading: ({ children, level }) => {
              if (level === 1) {
                return <Heading>{children}</Heading>
              }

              return <Heading2>{children}</Heading2>
            },
            image: ({ src, alt }) => {
              const caption =
                alt.indexOf("caption:") === 0
                  ? alt.split("caption:")[1].trim()
                  : null

              const img = images[src]
              console.log("img src is", src)

              return (
                <Surface gridColumn="screen" alignItems="center" marginY={20}>
                  <img src={img} alt={alt} width={800} />
                  {caption && (
                    <Text marginTop={5}>
                      <i>{caption}</i>
                    </Text>
                  )}
                </Surface>
              )
            },
            parsedHtml: ({ element }) => {
              if (element.type === "neuron") {
                return <TextNeuron {...element.props} />
              }

              return element
            },

            paragraph: ({ children }) => {
              const text = children[0].props.children
              if (
                isString(text) &&
                text.toLowerCase().indexOf("figure:") === 0
              ) {
                const figure = text.split(":")[1].trim()

                if (editing && editing !== figure) {
                  return (
                    <Surface background="rgba(0, 0, 0, 0.02)" padding={3}>
                      <Text>Not rendering so you can edit {editing}</Text>
                    </Surface>
                  )
                }
                if (figures[figure]) {
                  const El = figures[figure]
                  return <El />
                } else {
                  return (
                    <Surface
                      border
                      background="rgba(0, 0, 0, 0.02)"
                      paddingX={20}
                      paddingY={5}
                      marginY={20}
                    >
                      <Text opacity={0.8}>Missing figure {figure}</Text>
                    </Surface>
                  )
                }
              }

              return <Paragraph>{children}</Paragraph>
            },
          }}
        />
        {next && <Next {...next} />}
      </React.Fragment>
    )
  }
}
